"use client";

import { useState, useCallback, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Copy, RefreshCw, Shield, Settings, Check, Lock } from "lucide-react";
import Confetti from "@/components/animations/Confetti";
import { motion } from "framer-motion";
import Image from "next/image";
import ClientOnly from "@/components/common/ClientOnly";

interface PasswordOptions {
  length: number;
  includeUppercase: boolean;
  includeLowercase: boolean;
  includeNumbers: boolean;
  includeSymbols: boolean;
  googleWorkspaceCompatible: boolean;
}

export default function PasswordGenerator() {
  const [password, setPassword] = useState("");
  const [copied, setCopied] = useState(false);
  const [isGenerating, setIsGenerating] = useState(false);
  const [mounted, setMounted] = useState(false);
  const [showConfetti, setShowConfetti] = useState(false);

  const [options, setOptions] = useState<PasswordOptions>({
    length: 14,
    includeUppercase: true,
    includeLowercase: true,
    includeNumbers: true,
    includeSymbols: true,
    googleWorkspaceCompatible: false,
  });

  useEffect(() => {
    setMounted(true);
  }, []);

  const generatePassword = useCallback(
    async (showLoading = true, triggerConfetti = false) => {
      if (showLoading) setIsGenerating(true);

      // Simulation d'un délai pour l'animation
      await new Promise((resolve) => setTimeout(resolve, 500));

      const uppercase = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";
      const lowercase = "abcdefghijklmnopqrstuvwxyz";
      const numbers = "0123456789";
      const symbols = options.googleWorkspaceCompatible
        ? "!@#$%^&*()-_=+[]{}|;:,.<>?"
        : "!@#$%^&*()-_=+[]{}|;:,.<>?/~`";

      let chars = "";
      if (options.includeUppercase) chars += uppercase;
      if (options.includeLowercase) chars += lowercase;
      if (options.includeNumbers) chars += numbers;
      if (options.includeSymbols) chars += symbols;

      if (!chars) chars = lowercase;

      let result = "";
      if (typeof window !== "undefined" && window.crypto) {
        const array = new Uint8Array(options.length);
        window.crypto.getRandomValues(array);
        for (let i = 0; i < options.length; i++) {
          result += chars[array[i] % chars.length];
        }
      } else {
        for (let i = 0; i < options.length; i++) {
          result += chars[Math.floor(Math.random() * chars.length)];
        }
      }

      setPassword(result);
      if (showLoading) setIsGenerating(false);

      if (triggerConfetti) {
        setShowConfetti(true);
      }
    },
    [options]
  );

  const handleOptionChange = useCallback(
    (newOptions: Partial<PasswordOptions>) => {
      setOptions((prev) => {
        const updatedOptions = { ...prev, ...newOptions };

        if (password) {
          setTimeout(() => generatePassword(false, false), 0);
        }

        return updatedOptions;
      });
    },
    [password, generatePassword]
  );

  const copyToClipboard = async () => {
    if (!password || typeof window === "undefined") return;

    try {
      if (navigator.clipboard) {
        await navigator.clipboard.writeText(password);
      } else {
        // Fallback pour les navigateurs plus anciens
        const textArea = document.createElement("textarea");
        textArea.value = password;
        document.body.appendChild(textArea);
        textArea.select();
        document.execCommand("copy");
        document.body.removeChild(textArea);
      }

      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    } catch (err) {
      console.error("Erreur lors de la copie:", err);
    }
  };

  if (!mounted) {
    return (
      <div className="w-full max-w-5xl mx-auto">
        <div className="animate-pulse bg-card/50 rounded-3xl shadow-xl border border-border/30 h-96 flex items-center justify-center">
          <div className="text-muted-foreground">Chargement...</div>
        </div>
      </div>
    );
  }

  return (
    <div className="w-full max-w-5xl mx-auto">
      {/* Container principal avec glassmorphism */}
      <div className="relative bg-gradient-to-br from-card/95 via-card/90 to-card/85 backdrop-blur-xl rounded-3xl shadow-2xl border border-primary/20 overflow-hidden">
        {/* Effet de brillance subtil */}
        <div className="absolute inset-0 bg-gradient-to-r from-transparent via-primary/5 to-transparent opacity-50"></div>

        {/* Header élégant avec logo */}
        <div className="relative bg-gradient-to-r from-primary/15 via-primary/8 to-primary/15 p-8 border-b border-primary/20">
          <div className="flex items-center justify-between">
            {/* Logo et titre */}
            <div className="flex items-center space-x-4">
              <div className="relative">
                <div className="absolute inset-0 bg-primary/30 rounded-2xl blur-lg"></div>
                <div className="relative w-20 h-20 bg-gradient-to-br from-primary/20 to-primary/10 rounded-2xl flex items-center justify-center border border-primary/30">
                  <Image
                    src="/logo-mysecurepassword.png"
                    alt="MySecurePassword Logo"
                    width={72}
                    height={72}
                    className="object-contain drop-shadow-lg"
                    priority
                  />
                </div>
              </div>
              <div>
                <h1 className="text-3xl font-bold bg-gradient-to-r from-foreground to-foreground/80 bg-clip-text text-transparent">
                  MySecurePassword
                </h1>
                <p className="text-muted-foreground mt-1 font-medium">
                  Générateur sécurisé Google Workspace
                </p>
              </div>
            </div>

            {/* Badge RGPD modernisé */}
            <div className="flex items-center space-x-2 px-4 py-2 bg-gradient-to-r from-accent/20 to-accent/10 border border-accent/30 rounded-full backdrop-blur-sm">
              <div className="w-6 h-6 rounded-full bg-gradient-to-br from-blue-500 to-blue-600 flex items-center justify-center text-white text-xs font-bold">
                🇫🇷
              </div>
              <span className="text-accent-foreground font-semibold text-sm">
                RGPD Conforme
              </span>
            </div>
          </div>
        </div>

        {/* Contenu principal avec espacement moderne */}
        <div className="relative p-8 space-y-8">
          {/* Section mot de passe - Design premium */}
          <div className="space-y-6">
            {/* Titre avec icône élégante */}
            <div className="flex items-center space-x-3">
              <div className="w-10 h-10 bg-gradient-to-br from-primary/20 to-primary/10 rounded-xl flex items-center justify-center border border-primary/20">
                <Shield className="w-5 h-5 text-primary" />
              </div>
              <div>
                <h2 className="text-xl font-bold text-foreground">
                  Mot de passe sécurisé
                </h2>
                <p className="text-sm text-muted-foreground">
                  Généré avec chiffrement avancé
                </p>
              </div>
            </div>

            {/* Zone de mot de passe avec design glassmorphism */}
            <div className="relative group">
              <div className="absolute inset-0 bg-gradient-to-r from-primary/10 via-primary/5 to-primary/10 rounded-2xl opacity-0 group-hover:opacity-100 transition-opacity duration-300"></div>
              <div className="relative bg-gradient-to-r from-background/80 to-background/60 backdrop-blur-sm border-2 border-border/30 rounded-2xl p-1">
                <Input
                  type="text"
                  value={password}
                  readOnly
                  placeholder="Cliquez sur 'Générer' pour créer un mot de passe sécurisé..."
                  className="w-full h-16 text-lg font-mono bg-transparent border-0 text-foreground placeholder:text-muted-foreground/70 focus:ring-0 focus:outline-none px-6"
                />
                {password && (
                  <Button
                    onClick={copyToClipboard}
                    variant="ghost"
                    size="sm"
                    className="absolute right-3 top-1/2 transform -translate-y-1/2 h-12 px-4 bg-primary/10 hover:bg-primary/20 border border-primary/20 rounded-xl transition-all duration-200"
                  >
                    {copied ? (
                      <Check className="w-5 h-5 text-green-400" />
                    ) : (
                      <Copy className="w-5 h-5 text-primary" />
                    )}
                  </Button>
                )}
              </div>
            </div>

            {/* Bouton génération premium */}
            <motion.div whileHover={{ scale: 1.02 }} whileTap={{ scale: 0.98 }}>
              <Button
                onClick={() => generatePassword(true, true)}
                disabled={isGenerating}
                className="relative w-full h-16 text-lg font-bold bg-gradient-to-r from-primary via-primary/90 to-primary hover:from-primary/90 hover:via-primary/80 hover:to-primary/90 text-primary-foreground shadow-xl hover:shadow-2xl transition-all duration-300 border-0 rounded-2xl overflow-hidden group"
              >
                <div className="absolute inset-0 bg-gradient-to-r from-white/20 via-transparent to-white/20 opacity-0 group-hover:opacity-100 transition-opacity duration-300"></div>
                <div className="relative flex items-center justify-center">
                  {isGenerating ? (
                    <RefreshCw className="w-6 h-6 mr-3 animate-spin" />
                  ) : (
                    <Shield className="w-6 h-6 mr-3" />
                  )}
                  {isGenerating
                    ? "Génération en cours..."
                    : "Générer un mot de passe sécurisé"}
                </div>
              </Button>
            </motion.div>
          </div>

          {/* Section Configuration Premium */}
          <div className="space-y-6">
            {/* Titre Configuration */}
            <div className="flex items-center space-x-3">
              <div className="w-10 h-10 bg-gradient-to-br from-secondary/20 to-secondary/10 rounded-xl flex items-center justify-center border border-secondary/20">
                <Settings className="w-5 h-5 text-secondary-foreground" />
              </div>
              <div>
                <h2 className="text-xl font-bold text-foreground">
                  Configuration avancée
                </h2>
                <p className="text-sm text-muted-foreground">
                  Personnalisez votre mot de passe
                </p>
              </div>
            </div>

            {/* Grille de cartes de configuration */}
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
              {/* Carte Longueur */}
              <div className="bg-gradient-to-br from-card/60 to-card/40 backdrop-blur-sm border border-border/30 rounded-2xl p-6 space-y-4">
                <div className="flex items-center justify-between">
                  <h3 className="text-lg font-semibold text-foreground">
                    Longueur
                  </h3>
                  <div className="px-3 py-1 bg-primary/20 border border-primary/30 rounded-lg">
                    <span className="text-primary font-bold text-lg">
                      {options.length}
                    </span>
                    <span className="text-primary/80 text-sm ml-1">car.</span>
                  </div>
                </div>
                <input
                  type="range"
                  min="8"
                  max="64"
                  value={options.length}
                  onChange={(e) =>
                    handleOptionChange({ length: parseInt(e.target.value) })
                  }
                  className="w-full h-3 bg-gradient-to-r from-border/50 to-border/30 rounded-full appearance-none cursor-pointer slider-primary"
                  aria-label="Longueur du mot de passe"
                  title={`Longueur du mot de passe: ${options.length} caractères`}
                />
                <div className="flex justify-between text-xs text-muted-foreground">
                  <span>8 min</span>
                  <span>64 max</span>
                </div>
              </div>

              {/* Carte Types de caractères */}
              <div className="bg-gradient-to-br from-card/60 to-card/40 backdrop-blur-sm border border-border/30 rounded-2xl p-6 space-y-4">
                <h3 className="text-lg font-semibold text-foreground">
                  Types de caractères
                </h3>
                <div className="grid grid-cols-2 gap-3">
                  {[
                    {
                      key: "includeUppercase",
                      label: "Majuscules",
                      desc: "A-Z",
                      icon: "🔤",
                    },
                    {
                      key: "includeLowercase",
                      label: "Minuscules",
                      desc: "a-z",
                      icon: "🔡",
                    },
                    {
                      key: "includeNumbers",
                      label: "Chiffres",
                      desc: "0-9",
                      icon: "🔢",
                    },
                    {
                      key: "includeSymbols",
                      label: "Symboles",
                      desc: "!@#",
                      icon: "⚡",
                    },
                  ].map(({ key, label, desc, icon }) => (
                    <label
                      key={key}
                      className="group flex items-center space-x-3 p-3 bg-gradient-to-br from-background/50 to-background/30 border border-border/20 rounded-xl hover:border-primary/30 cursor-pointer transition-all duration-200"
                    >
                      <input
                        type="checkbox"
                        checked={
                          options[key as keyof PasswordOptions] as boolean
                        }
                        onChange={(e) =>
                          handleOptionChange({ [key]: e.target.checked })
                        }
                        className="w-5 h-5 text-primary border-border/50 rounded-lg focus:ring-primary focus:ring-2 transition-colors"
                      />
                      <div className="flex-1">
                        <div className="flex items-center space-x-2">
                          <span className="text-lg">{icon}</span>
                          <div className="text-sm font-medium text-foreground">
                            {label}
                          </div>
                        </div>
                        <div className="text-xs text-muted-foreground mt-1">
                          {desc}
                        </div>
                      </div>
                    </label>
                  ))}
                </div>
              </div>
            </div>

            {/* Mode Google Workspace - Carte spéciale */}
            <div className="bg-gradient-to-r from-accent/10 via-accent/5 to-accent/10 border-2 border-accent/20 rounded-2xl p-6">
              <label className="flex items-start space-x-4 cursor-pointer group">
                <input
                  type="checkbox"
                  checked={options.googleWorkspaceCompatible}
                  onChange={(e) =>
                    handleOptionChange({
                      googleWorkspaceCompatible: e.target.checked,
                    })
                  }
                  className="w-6 h-6 text-accent border-accent/30 rounded-lg focus:ring-accent focus:ring-2 mt-1 transition-colors"
                />
                <div className="flex-1">
                  <div className="flex items-center space-x-2 mb-2">
                    <span className="text-xl">🏢</span>
                    <h3 className="text-lg font-semibold text-foreground">
                      Mode Google Workspace
                    </h3>
                  </div>
                  <p className="text-sm text-muted-foreground leading-relaxed">
                    Optimise la compatibilité en excluant les caractères
                    problématiques pour Google Workspace, garantissant un
                    fonctionnement parfait avec tous les services Google.
                  </p>
                </div>
              </label>
            </div>
          </div>
        </div>
      </div>

      {/* Confetti */}
      <Confetti
        isActive={showConfetti}
        onComplete={() => setShowConfetti(false)}
      />
    </div>
  );
}
